
.navbar{
    
    position: fixed;
    top: 0;
    padding: 20px;
    width: 100%;
    height: 30px;
    z-index: 1;
    transition-timing-function: ease-in;
    transition: all 0.5s;
}
.nav_black{
    background-color: black;
}
.nav_contents{
    display: flex;
    justify-content: space-between;
}
.logo{
    position: fixed;
    left:0;
    top:11px;
    width: 80px;
    object-fit: contain;
    padding-left: 20px;
    cursor: pointer;
}

.nav_avatar{
    position: fixed;
    right:20px;
    width: 30px;
    cursor: pointer;
}