.banner{
    height:440px;
    object-fit: contain;
    position: relative;
    display: flex;
    flex-direction: column;
}
.banner_contents{
    margin-left: 30px;
    margin-top: 120px;
    
}
.banner_title{
    font-size: 3rem;
    font-weight:800 ;
    color:white;
    padding-bottom: 0.3rem;
}
.banner_desc{
    width:50rem;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: 0.5rem;
    max-width: 360px;
    height: 80px;
    color:white;
}
.banner_btn_class{
    cursor: pointer;
    color:#fff;
    outline: none;
    border:none;
    font-weight: 700;
    border-radius: 0.2vw;
    padding-left: 2rem;
    padding-right: 2rem;
    margin-right: 1rem;
    padding-top:0.5rem ;
    background-color: rgba(51, 51, 51,0.5);
    padding-bottom: 0.5rem;
}
.banner_btn_class:hover{
    color:black;
    background-color: #fff;
    transition:all 0.2s;
}
.banner_bottom{
    height:11.4rem;
    background-image:linear-gradient(180deg,transparent,rgba(37,37,37,0.611),#111) ;
}